@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700;800&display=swap);
body {
  width: 100%;
  max-width: 100em;
  margin: 0 auto; /* Center alignment */
  font-family: 'Montserrat', sans-serif;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Page Best*/
.product__best {
  display:inline-block;
  background-image: url(/what-happened-market/static/media/bes-bg.08b8b84f.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 60rem;
  text-align: center;
  width: 100%;
}

.title__best {
  color: #f96400;
  margin: 7rem 0 0 0;
  margin: auto;
  margin-top: 7rem;
  width: 40%;
  font-weight: bold;
}

/* best_item */
.item__best {
  display: inline-flex;
  width: 80%;
  margin-top: 2.5rem
}

/* End bestitem */

/* End Page Best */

.main-banner {
    display: flex;
    background-image: url(/what-happened-market/static/media/main-banner.b48c79d1.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    min-height: 52rem;

}
.nav {  
    background: #1f1f1f;  
    padding: 0 2rem;
}
.row{
    display: flex;
    justify-content: space-between;
}
.top-bar__links{
    display: flex;
    align-items: center;
    padding: 1.2rem 0;
    margin: 0;
    list-style-type: none;
}
.top-bar__links > li > a,
.top-bar__links > li > p{
    color: white;
    margin: 0 1rem;
    text-decoration: none;
}
.col-logo {
    padding: 1rem 0;
    text-align: center;
}
  

.banner {
 
    display: flex;
    background-image: url(/what-happened-market/static/media/introduction-bg.1563ce83.png);
    background-repeat: no-repeat;
    background-size: cover;
    height: 29rem;
    object-fit: contain;
}

.content {
    margin: 5.7rem 0 8rem 8.6rem;
    height: 8rem;
    width: 36%;
    color: #fff;
}

.title {
    font-weight: 800;
    font-size: 2.5rem;
    letter-spacing: 0.3rem;
    margin-bottom: 1.5rem;
}

.description {
    font-size: 1rem;
    line-height: 1.75;
}

